import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId,  fetchEncryptStream, ObjectKeys,  parseFloatToDecimal, qryStore, generateComprobanteId, hasWritePrivilege } from "../../../stores/Utils";

import {  TextInput_Cmpt, CheckBox_Cmpt, SvgIcon_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal } from "../../../components/DialogHRM";
import { SelectEfectivoAcc_Cmpt } from "..";
import { SumDocuments, getall_collects } from "../services/server_fetching";
import { DateCalComponent_Cmpt } from "../../Inventory";
import { useNavigate } from "react-router";

const Svg = SvgIcon_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();

const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();


const frmNm = "new_cobro2_comprobante"


const CobroManualView  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const navigate = useNavigate();

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("accountPicker", {});
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);

		let comp2save = [];

		if(!ffm["date"]){
			ffm["date"] = (new Date()).getTime()+800;
		}
		else{
			ffm["date"] =(new Date(ffm["date"])).getTime();
		}


		let comprobanteId = await generateComprobanteId(ffm["date"]);
		ffm["comprobanteId"] = comprobanteId;

		
		let reference="";
		let arr = getStore("multi_collect_by_doc") && ObjectKeys(getStore("multi_collect_by_doc"));
		arr && arr?.map((flds, indE)=>{

			let nnfrm = {...ffm};
			let amount = getStore("multi_collect_by_doc")[flds];

			nnfrm["document"] = flds;
			reference += flds +" ; ";
			nnfrm["account"] = getStore("Acc2Collect")?.account;
			nnfrm["subAccount"] = getStore("Acc2Collect")?.subAccount;
			nnfrm["customerId"] = getStore("Acc2Collect")?.customerId;
			nnfrm["type"] = "credit";
			nnfrm["amount"] = amount*1;
			nnfrm["payOrCollect"] = "collect";
			nnfrm["operationId"] = gen16CodeId();
			
			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;
			comp2save.push(bdyq2)
			
		});

		let nnfrm = {...ffm};
		let amount = SumCollects(getStore("multi_collect_by_doc"));

		nnfrm["document"] = nnfrm["collectDocument"];
		nnfrm["collectDocument"]= reference;
		nnfrm["PaymentMethodType"]= nnfrm.paymentType;
		nnfrm["account"] = getStore("global_account_params")?.cash_bank?.account;
		nnfrm["type"] = "debit";
		nnfrm["amount"] = amount*1;
		nnfrm["operationId"] = gen16CodeId();
		let bdyq2 = {...getStore(qryStore())?.add_opera};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId
		}
		bdyq2.form=nnfrm;


		comp2save.push(bdyq2)
		//console.log(comp2save)
		
		
		const promises = comp2save.map(async (qry2Sv) => {
			const res = fetchEncryptStream(qry2Sv);
			const td = await res;
			if (td) {
				//console.log(td)
			}
        })      
        await Promise.all(promises);

		
		
		
		setTimeout(() => {
			updStore("multi_collect_by_doc", null)
			updStore(frmNm, null)
			set_obs(gen16CodeId());
			confirm && confirm();
			if(hasWritePrivilege()){
				data?.modalID && CloseModal({id:data?.modalID});
				navigate({
					pathname: "/accounting-ledger" ,
					search: `?cId=${comprobanteId}`
				});
			}
			else{
				getall_collects(getStore("Acc2Collect"), ()=>{
					data?.modalID && CloseModal({id:data?.modalID});
				});
			}
		}, 400);
		
		/* */
	};




	let hasDoc2PorC  = SumDocuments(getStore("collect_by_doc"),"amount")
	
	
	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "720px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Registrando Cobro
				</p>
				<div className="flexSpace"/>
				
			</div>

			
			<div className="_dsplFlx ">
				<div className="_dsplFlx " style={{padding: "35px 18px 10px "}}>
					<div className="flexSpace"/>
					<CheckBox 
						data={[
							{label: "Cheque", id:"check"},
							{label: "Tarjeta", id:"card"}
							
						]}
						active={getStore(frmNm)?.["paymentType"]}
						updSelect={(e)=>updFrm("paymentType",e)}
					/>
					<div className="flexSpace"/>
				</div>
				
			</div>

			
			<div className="_dsplFlx ">
				<DateCalComponent init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
               
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["collectDocument"]} 
					label={"Documento de Cobro"} 
					width={200} 
					updChanges={(e)=>updFrm("collectDocument",e)}
				/>
			</div>

			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>

			

			
			{hasDoc2PorC ? 
			<div className="boxAddAcc" style={{margin: "18px 0"}}>

				<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
					<div className="tr_bar tr_bar_40">
						Documento
					</div>
					<div className="tr_bar tr_bar_20">
						Balance
					</div>
					<div className="tr_bar tr_bar_10">
						
					</div>
					<div className="tr_bar tr_bar_25">
						Por Cobrar
					</div>
				</div>
				{getStore("collect_by_doc") && ObjectKeys(getStore("collect_by_doc"))?.map((element, indE)=>
				
					<ListingAccounts 
						key={element} 
						idKey={element} 
						data={getStore("collect_by_doc")[element]}
						updObs={set_obs}
					/>
				)}
			</div>
			:null}
			
			{IsUnder(getStore("collect_by_doc"))?
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>
			</div>
			:null}
		
			
		</div>
	);
};

export default CobroManualView;


const ListingAccounts  = ({ data, updObs, idKey }) => {
	const [amount, set_amount] = useState(0);



	const handleChange = (v) => {
		set_amount(v);
		updFrm(data?.document,v);
		updObs(gen16CodeId())
	}
	
	
	
	const updFrm = (f, v, k) => {
		let frm2 = getStore("multi_collect_by_doc") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("multi_collect_by_doc", frm2);
	};

	
	if(data?.amount*1 == 0){
		return null;
	}  

	return (

		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar tr_bar_40" style={{marginTop: 10}}>
				{data?.document }
			</div>
			<div className="tr_bar tr_bar_20" style={{marginTop: 10}}>
				${(data?.amount*1).toFixed(2) }
			</div>
			<div className="tr_bar tr_bar_10" style={{marginTop: 10}}>
				{amount > data?.amount*1 ? 
				<div className="icon" >
					<Svg name={"alert"} color={"#c62828"}/>
				</div>
				:null}
			</div>
			
			<div className="tr_bar tr_bar_25">
				<TextInput  
					init={amount} 
					//label={"Descripcion"} 
					width={80} 
					type={"number"}
					updChanges={(e)=>handleChange(e)}
				/>
			</div>
		</div>
	)

}



const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let ammm =  parseFloatToDecimal(data?.amount,2);
		if(data?.type === "debit"){ 
			balance += ammm;
		}
		else if(data?.type === "credit"){ 
			balance -= ammm;
		}
	});
	return arr && arr?.length>0 && balance === 0;
}  




const SumCollects = (obj) => {
	let balance = 0;
	obj && ObjectKeys(obj)?.map((flds, indE)=>{
		balance =  parseFloatToDecimal(balance,2)*1 + parseFloatToDecimal(obj[flds],2)*1;
	});
	return  balance;
}  



const IsUnder = (obj) => {
	let arr = obj && ObjectKeys(obj);
	let frms = getStore("multi_collect_by_doc");
	let isValid = true;
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let ammm =  parseFloatToDecimal(data?.amount*1,2);
		let amount2Pay = frms?.[data["document"]] ? parseFloatToDecimal(frms[data["document"]]*1,2): 0;
		
		if(amount2Pay && amount2Pay - ammm > 0){
			
			isValid = false;
		}
	});
	return isValid;
}  
